@use 'base/mixins' as m;
@use 'base/variables' as v;


.filtros{

    background-color: v.$blanco;
    margin-top: 2rem;
    border-radius: 1rem;
    padding: 2rem;
    .filtros-inputs{
        @include m.tablet{
            display: flex;
            align-items: center;
            gap: 2rem;
        }
    }

    h2{
        color: v.$indigo;
        font-size: 2.8rem;

        //para que quede alineado, y no mas arriba que el resto de los componentes
        @include m.tablet{
            margin: 0;
        }
    }

    .campo{
        margin-bottom: 2rem;
        display: flex;
        justify-content: space-between;

        //para escritorio
        align-items: center;
        gap: 1rem;
        @include m.tablet{
            margin-bottom: 0;
        }
    }

    input[type="radio"]{
        accent-color: red;
        width: 1.6rem;
        height: 1.6rem;
    }
}